/*--------------------------------------------*/
/* reset */
/*--------------------------------------------*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/*--------------------------------------------*/
/* common */
/*--------------------------------------------*/
html, body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

*, *:before, *:after {
  box-sizing: inherit;
}

a {
  color: inherit;
}

body {
  margin: 0;
  padding: 0;
  background-color: #EFEFEF;
}

canvas {
  display: block;
  top: 0;
  left: 0;
}

#banner {
  /* bigbox */
  width: 300px;
  height: 250px;
  overflow: hidden;
  position: relative;
  background-color: #ffcc00;
  -webkit-text-size-adjust: 100%;
  opacity: 1;
  cursor: pointer;
  display: inline-block;
}

#banner > a {
  width: inherit;
  height: inherit;
}

canvas, .text, .frame, .outline, img {
  position: absolute;
}

img {
  vertical-align: top;
  -webkit-backface-visibility: hidden;
  -webkit-transform: translateZ(0) scale(1, 1);
}

.outline {
  z-index: 99;
  width: inherit;
  height: inherit;
  border: 1px solid black;
  pointer-events: none;
  position: absolute;
}

.frame {
  width: inherit;
  height: inherit;
  opacity: 0;
  top: 0;
  left: 0;
  background-color: #ffcc00;
}

p {
  display: inline-block;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

sup {
  font-size: 50%;
  vertical-align: top;
  position: relative;
  left: -3px;
}

.shadow {
  -webkit-filter: drop-shadow(5px 5px 9px black);
  filter: drop-shadow(5px 5px 9px black);
}

.retina {
  -webkit-transform: translate(0px, 0px) scale(0.5, 0.5);
  -moz-transform: translate(0px, 0px) scale(0.5, 0.5);
  -ms-transform: translate(0px, 0px) scale(0.5, 0.5);
  transform: translate(0px, 0px) scale(0.5, 0.5);
  backface-visibility: hidden;
  perspective: 1000px;
  position: absolute;
  transform-origin: 0 0;
}

#legalBtn:hover ~ #legalContent {
  opacity: 1;
}

#legalContent {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.9);
  -webkit-transform: translate(0px, 0px);
  -moz-transform: translate(0px, 0px);
  -ms-transform: translate(0px, 0px);
  transform: translate(0px, 0px);
  backface-visibility: hidden;
  perspective: 1000px;
  position: absolute;
  color: white;
  font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
  padding: 10px;
  font-size: 10px;
  z-index: 90;
  opacity: 0;
  font-weight: normal;
  line-height: 14px;
  transition: opacity 0.3s;
  pointer-events: none;
}

.hide {
  opacity: 0;
  -webkit-transform: translate(0px, 0px);
  -moz-transform: translate(0px, 0px);
  -ms-transform: translate(0px, 0px);
  transform: translate(0px, 0px);
  backface-visibility: hidden;
  perspective: 1000px;
  position: absolute;
  z-index: 0;
  pointer-events: none;
}

#legalBtn {
  -webkit-transform: translate(0px, 0px);
  -moz-transform: translate(0px, 0px);
  -ms-transform: translate(0px, 0px);
  transform: translate(0px, 0px);
  backface-visibility: hidden;
  perspective: 1000px;
  position: absolute;
  opacity: 1;
  width: 36px;
  height: 25px;
  background-color: red;
  opacity: 0;
}

.logo_hwc img {
  width: 100%;
  height: 100%;
}

@-webkit-keyframes rotating_keyframes {
  from {
    -webkit-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

@keyframes rotating_keyframes {
  from {
    -ms-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -webkit-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  to {
    -ms-transform: rotate(360deg);
    -moz-transform: rotate(360deg);
    -webkit-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

.rotating {
  -webkit-animation: rotating_keyframes 16s linear 2;
  -moz-animation: rotating_keyframes 16s linear 2;
  -ms-animation: rotating_keyframes 16s linear 2;
  -o-animation: rotating_keyframes 16s linear 2;
  animation: rotating_keyframes 16s linear 2;
}

.logo_hwc {
  position: absolute;
}

.line {
  -webkit-transform: translate(0px, 0px) scale(0.5, 0.5);
  -moz-transform: translate(0px, 0px) scale(0.5, 0.5);
  -ms-transform: translate(0px, 0px) scale(0.5, 0.5);
  transform: translate(0px, 0px) scale(0.5, 0.5);
  backface-visibility: hidden;
  perspective: 1000px;
  position: absolute;
}

.frame1 {
  opacity: 0;
}

.line_2a, .t2 {
  -webkit-transform: translate(0px, -3px) scale(0.5, 0.5);
  -moz-transform: translate(0px, -3px) scale(0.5, 0.5);
  -ms-transform: translate(0px, -3px) scale(0.5, 0.5);
  transform: translate(0px, -3px) scale(0.5, 0.5);
  backface-visibility: hidden;
  perspective: 1000px;
  position: absolute;
}

